import React, { Fragment, useEffect, useState } from 'react'
import Pagination from './Pagination';
import Post from './Post';
import {getPost, searchPost} from '../../../_actions/post_action'
import { useDispatch } from 'react-redux';
import { Link, useNavigate } from 'react-router-dom';
import styled from 'styled-components'
import SearchBox from '../../SearchPage/SearchBox';
import axios from 'axios';

const Center = styled.div`
    
    display: grid;
    justify-items: center;justify-items: center;
    min-height: 100vh;
`
const Button = styled.button`
    border: none;
    border-radius: 7px;
    background-color: ${(props) =>{ 
            return props.theme.color.navy
        }
    }};
    color:${(props) => props.theme.color.white};
    padding: 8px 16px;
    font-size: ${(props) => props.theme.fontSizes.base};
    text-align: center;
    cursor: pointer;
    margin: 5px;
`
const StyledDiv = styled.div`
    height: 400px;
    width: 85%;
`
const LogoutDiv = styled(StyledDiv)`
    height: 50px;
    width: 85%;
`



function Board({params, userId}) {
    const [Skip, setSkip] = useState(0)
    const Limit = 8;//  post  

    const [CurrentPage, setCurrentPage] = useState(1);
    const [Posts, setPosts] = useState([])

    const dispatch = useDispatch()
    const navigate=useNavigate()
    // console.log(params)
    useEffect(() => {
        const body = {
            skip: Skip,
            limit: Limit
        }
        if(params) {
            searchPostData(body);
        } else {
            getPostData(body);
        }
        
    
    
    }, [CurrentPage, Skip, Limit, params])

    const logoutkHandler = async () => {
        const request = await axios.get('/api/users/logout');
        console.log(request.data)
        if(request.data.logoutSuccess === false) {
            alert('failed to logout')
        } else {
            navigate('/posts')
        }
        
    }
    const searchPostData = () => {
        const body = {
            keyword: params.keyword,
            search_type: params.search_type,
            skip: Skip,
            limit: Limit
        }
        

        dispatch(searchPost(body))
            .then(res => {
                setPosts(res.payload.posts)
            })
    }
    
    const getPostData = (body) => {
        // axios.post('/api/post/getPosts', body).then(
        //     res => console.log(res.data)
        // )
        dispatch(getPost(body))
            .then(res => {
                // console.log(res.payload.posts)
                setPosts(res.payload.posts)
            })
    }
    const onPageChange = (pageNum) => {
        setCurrentPage(pageNum)
        setSkip((pageNum - 1) * Limit)
        //2   3  3 post 
    }
    
    // console.log(userId)

    const Loginbtn = () => {
        return (
            userId ?
            <Fragment>
                <Link to='/posts'><Button></Button></Link>
                <Link to='/posts/write'><Button></Button></Link>
                <Button onClick={logoutkHandler}></Button>
            </Fragment>
            
            :<Link to='/posts'><Button></Button></Link>
        )
    }
    const Logoutbtn =() => {
        return (
            userId ?
            null
            :<Fragment>
                <Link to='/users/login'><Button></Button></Link>
                <Link to='/users/register'><Button></Button></Link>
            </Fragment>
        )
    }
    return (
        <Center>
            <LogoutDiv>
                {
                    Logoutbtn()
                }
            </LogoutDiv>
            
            {/* params searchbox.js   */}
            <Post posts={Posts}/>
            
            <StyledDiv>
                {
                    Loginbtn()
                }
                <Pagination currentPage={CurrentPage} onPageChange={onPageChange} limit={Limit} params={params}/>
                
                <SearchBox />
            </StyledDiv>
        </Center>
        
    )
}
export default Board